// файл promo.scss
.promo {
  position: relative;
  padding: 29px 0 22px 0;
  font-family: $additional-font;
  text-align: center;
  color: $white;
  background-image: url("../img/index-background-mobile@1x.jpg");
  background-color: $white;
  background-repeat: no-repeat;
  background-size: 100%;

  @media (min-resolution: $retina-dpi),
    (min-resolution: $retina-dppx) {
    background-image: url("../img/index-background-mobile@2x.jpg");
    background-size: 100%;
  }

  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: column;
    padding: 61px 30px 0 0;
    background-image: none;
    background-color: $white;
    text-align: left;
  }

  @media (min-width: $desktop-width) {
    position: relative;
    padding: 31px 30px 0 0;
    min-height: 656px;
    background-position: right;
  }

  &::before {
    width: 50vw;

    @media (min-width: $desktop-width) {
      content: "";
      position: absolute;
      margin-top: -133px;
      margin-right: 1px;
      top: 0;
      right: 0;
      z-index: 10;
      height: 696px;
      background-image: url("../img/index-background-desktop@1x.jpg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
    }

    @media (min-resolution: $retina-dpi),
      (min-resolution: $retina-dppx) {
      background-image: url("../img/index-background-desktop@2x.jpg");
    }
  }
}

.promo__title {
  margin: -3px 0 26px 0;
  font-size: 36px;
  font-weight: 400;
  line-height: 36px;

  @media (min-width: $tablet-width) {
    margin: -24px 0 42px 88px;
    width: 415px;
    color: $gray-darken;
    z-index: 15;

    @include subtitle-tablet();
  }

  @media (min-width: $desktop-width) {
    margin: 57px 0 33px 95px;
  }
}

.promo__slogan {
  margin: 0 0 28px 0;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    margin: 0 0 29px 88px;
    font-size: 20px;
    line-height: 20px;
    z-index: 15;
    color: $gray-darken;
  }

  @media (min-width: $desktop-width) {
    margin: 42px 0 53px 95px;
    font-size: 20px;
    line-height: 20px;
  }
}

.promo__image {
  margin: 0;

  @media (min-width: $tablet-width) {
    margin: -9px 30px -5px -1px;
    width: 709px;
    height: auto;
    order: 2;
  }

  @media (min-width: $desktop-width) {
    position: absolute;
    top: -13px;
    right: 214px;
    width: 550px;
    height: auto;
    z-index: 10;
  }
}

.promo__button {
  display: inline-block;
  height: 41px;
  width: 281px;
  padding: 11px 9px 8px 7px;
  font-size: 14px;
  font-family: $additional-font;
  text-align: center;
  text-transform: uppercase;
  color: $white;
  background-color: $apple;
  border-style: none;

  @media (min-width: $tablet-width) {
    position: absolute;
    top: 181px;
    left: 0;
    height: 54px;
    width: 245px;
    margin: 26px 0 0 119px;
    padding: 5px 10px 8px 7px;
    font-size: 20px;
    line-height: 20px;
  }

  @media (min-width: $desktop-width) {
    margin: 111px 0 0 135px;
    min-width: 245px;
    min-height: 52px;
    font-size: 20px;
    line-height: 20px;
  }
}

.promo__wrapper {
  position: relative;
}
